<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      label-width="80px"
    >  
     <el-row>
                    <el-col :span="12">
           <el-form-item v-if="flag=='weixiuyuan'"  label='' prop="weixiuyuanUuidNumber">
               <el-input v-model="ruleForm.weixiuyuanUuidNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='weixiuyuan'"  label='' prop="weixiuyuanName">
               <el-input v-model="ruleForm.weixiuyuanName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='weixiuyuan'"  label='' prop="weixiuyuanNianxian">
               <el-input v-model="ruleForm.weixiuyuanNianxian"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='weixiuyuan'"  label='' prop="weixiuyuanPhone">
               <el-input v-model="ruleForm.weixiuyuanPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='weixiuyuan'" label='' prop="weixiuyuanPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.weixiuyuanPhoto?ruleForm.weixiuyuanPhoto:''"
                         @change="weixiuyuanPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="24">
             <el-form-item v-if="flag=='weixiuyuan'"  label="" prop="weixiuyuanContent">
                 <editor
                         style="min-width: 200px; max-width: 600px;"
                         v-model="ruleForm.weixiuyuanContent"
                         class="editor"
                         action="file/upload">
                 </editor>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='xuesheng'"  label='' prop="xueshengUuidNumber">
               <el-input v-model="ruleForm.xueshengUuidNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='xuesheng'"  label='' prop="xueshengName">
               <el-input v-model="ruleForm.xueshengName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='xuesheng'"  label='' prop="xueshengIdNumber">
               <el-input v-model="ruleForm.xueshengIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='xuesheng'"  label='' prop="xueshengPhone">
               <el-input v-model="ruleForm.xueshengPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='xuesheng'" label='' prop="xueshengPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.xueshengPhoto?ruleForm.xueshengPhoto:''"
                         @change="xueshengPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-form-item v-if="flag=='users'" label="" prop="username">
             <el-input v-model="ruleForm.username"
                       placeholder=""></el-input>
         </el-form-item>
         <el-col :span="12">
             <el-form-item v-if="flag!='users'"  label="" prop="sexTypes">
                 <el-select v-model="ruleForm.sexTypes" placeholder="">
                     <el-option
                             v-for="(item,index) in sexTypesOptions"
                             v-bind:key="item.codeIndex"
                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-col :span="24">
             <el-form-item>
                 <el-button type="primary" @click="onUpdateHandler"> </el-button>
             </el-form-item>
         </el-col>
     </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
        ruleForm: {},
        flag: '',
        usersFlag: false,
        // sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
    };
  },
  mounted() {
    //
    var table = this.$storage.get("sessionTable");
    this.sessionTable = this.$storage.get("sessionTable");
    this.role = this.$storage.get("role");
    if (this.role != ""){
    }

    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
//  
//  
      } else {
        this.$message.error(data.msg);
      }
    });

//   
//   

      this.$http({
          url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=sex_types`,
          method: "get"
      }).then(({ data }) => {
          if (data && data.code === 0) {
              this.sexTypesOptions = data.data.list;
          } else {
              this.$message.error(data.msg);
          }
      });
  },
  methods: {
    chongzhi() {
      this.$router.replace({ path: "/pay" });
    },
    weixiuyuanPhotoUploadChange(fileUrls) {
        this.ruleForm.weixiuyuanPhoto = fileUrls;
    },
    xueshengPhotoUploadChange(fileUrls) {
        this.ruleForm.xueshengPhoto = fileUrls;
    },


    onUpdateHandler() {
                         if((!this.ruleForm.weixiuyuanUuidNumber)&& 'weixiuyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.weixiuyuanName)&& 'weixiuyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.weixiuyuanNianxian)&& 'weixiuyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.weixiuyuanPhone)&& 'weixiuyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.weixiuyuanPhoto)&& 'weixiuyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.weixiuyuanContent)&& 'weixiuyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.xueshengUuidNumber)&& 'xuesheng'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.xueshengName)&& 'xuesheng'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.xueshengIdNumber)&& 'xuesheng'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'xuesheng' ==this.flag && this.ruleForm.xueshengPhone&&(!isMobile(this.ruleForm.xueshengPhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.xueshengPhoto)&& 'xuesheng'==this.flag){
                             this.$message.error('');
                             return
                         }

        if((!this.ruleForm.sexTypes) && this.flag!='users'){
            this.$message.error('');
            return
        }
      if('users'==this.flag && this.ruleForm.username.trim().length<1) {
        this.$message.error(``);
        return	
      }
      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
